






















import Vue from 'vue';

export default Vue.extend({
  props: {
    name: {
      type: String
    },
    opened: {
      type: Boolean,
      default: true
    }
  },
  computed: {

    // possibility to define accordion default open/closed
    openedValue(): boolean {
      return this.opened;
    }
  },
  methods: {
    toggleOpened() {
      this.$emit('update:opened', !this.opened);
    }
  }
});



