































import Vue from 'vue';
import {GENERATE_PRESENTATION_SINGLE} from '../../../store/modules/projects/action-types';

export default Vue.extend({
  methods: {
    generatePresentation() {
      this.$store.dispatch(GENERATE_PRESENTATION_SINGLE);
    },
    deleteProject() {
      this.$emit('delete');
    }
  }
});



