


















import Vue from 'vue';

export default Vue.extend({
  props: {
    name: String,
    checked: Boolean
  },
  computed: {
    checkedValue(): boolean {
      return this.checked;
    }
  },
  methods: {
    toggleCheck() {
      this.$emit('update:checked', !this.checked);
    }
  }
});



