






































import Vue from 'vue';

export default Vue.extend({
  methods: {
    exit() {
      this.$emit('exit');
    },
    confirm() {
      this.$emit('confirm');
    }
  },
  created() {
    document.documentElement.classList.add('is-clipped');
  },
  destroyed() {
    document.documentElement.classList.remove('is-clipped');
  }
});



